import React from "react";
import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import { Fade } from "react-awesome-reveal";

export const Showcase: React.FC = () => {
  const { t } = useTranslation();
  return (
    <div>
      <div className="showcase">
        <div className="container showcase-inside">
          <div className="showcase-text">
            <div>
              <h4>{t("Hi, my name is Ibrohim")}</h4>
              <br />
              <h1>I'm Frontend developer</h1>
              <br />
              <p>
                I have been doing professional website development for 1 years
                already. Over the years of his activity he has created websites
                of different complexity and types.
              </p>
              <Link className="active learn-more" to="/about">
                Learn more <span className="span-animate">&gt;</span>
              </Link>
            </div>
          </div>
          <div className="hero"></div>
        </div>
      </div>
      <h1 className="title">Videos</h1>
      <div className="video">
        <div className="video-wrapper">
          <iframe
            src="https://www.youtube.com/embed/xcGtfYUfDLo"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          ></iframe>
        </div>
      </div>
    </div>
  );
};
